import './App.css';
import { Parallax, ParallaxLayer} from '@react-spring/parallax';
import React, {useState} from 'react';
import BG1 from './assets/bg1.jpg';

//page1
import TypeWriterEffect from 'react-typewriter-effect';

//page2-3
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';

//page4
import TinderCard from 'react-tinder-card';
import './styles.css'
import u2 from './assets/u2.png';
import u3 from './assets/u3.png';
import u4 from './assets/u4.png';
import u5 from './assets/u5.png';

//page5
import profile from './assets/profile.jpg';




//scrolling
//containers for navigation
function App() {

   const UIUX = [u5, u2, u4, u3];

   const [index, setIndex] = useState(0);
   const swe = [
    {url:u3, title:"Telebot - Walk SomeMore", 
      des:'Want to make it for the next bus but you are unsure how fast your pace needs to be? Check out the Walk Some-More telebot - a customised pacer that is guaranted to get you in time for the bus! This telebot retrieves real time bus timings from the LTA API and calculates the pace necessary to ensure you hop on the next bus! Just simply walk according to the pace set by the video sent by the telebot!', 
      link:"https://devpost.com/software/WalkSomeMore"},
    {url:u4, title:"Tinder? Nope Hair Checker", 
      des:'NSFs (National Servicemen Full-Time) are required to maintain a trim and proper haircut. Often, haircuts and hair lengths we deem to be acceptable do not meet the requirements of defence force. NSFs can now upload a picture of their haircut and the AI will estimate if the haircut is acceptable. The dataset used to train the AI will be provided by commanders who will swipe left if a haircut is unacceptable and right otherwise. ', 
      link:'https://github.com/thebadone231/ns-journey'}, 
    {url:u2, title:"plus-one", 
      des:'Ever wanted that bubbletea so badly but you are unwilling to pay the exorbitant delivery price? What if the aunty living next door happens to be near the bubbletea store and she is willing to bring back the beverage for a small fee? Plus-one promises to be an affordable substitute for delivery services. Pay a small fee for someone to run an errand on your behalf or get rewarded when you complete errand request on behalf of others!', 
      link:'https://github.com/thebadone231/plus-one'}, 
    {url:u5, title:"N queens? N kings, N knights, N bishops....", 
      des:'This is an adaptation of the famous N queens problem where there are n pieces asides from the queen. This is a project of the CS3243 Introduction to Artificical Intelligence module. By framing this scenario as a constraint satisfaction problem, it can be resolved by running the backtracking algorithm - recursive DFS with forward check / AC3 algorithm', 
      link:''},  
  ]

    return (
      <div style={{height:"100vh", backgroundImage:`url(${BG1})`, backgroundSize:'100% 100%'}}>

        <Parallax pages={5}>

          {/*page 1 - typewriter effect*/}
          <ParallaxLayer>
            <div style={{display:'flex', height:"100vh", alignItems: 'center', justifyContent:'center', }}>
              <div style={{display: 'flex', flexDirection: 'column', width:"65%", height:'30vh', alignItems:'center'}}>
                
                <div style={{height: "70%", width:'80%', fontSize: 40, display:'flex', alignItems: 'center', color:'black'}}>
                  <h1>Hi, I am Zheng Xi</h1>
                </div>

                <div style={{height: "30%", width: "70%", display:'flex', alignItems:'center', color:'white'}}>
                  <TypeWriterEffect
                    textStyle={{color: 'white', fontWeight: 500, fontSize: '1.5em',}}
                    cursorColor="white"
                    multiText={['I code', 'I do UI/UX', 'I am a computer engineer']}
                    typeSpeed={40}/>
                </div>
              </div>
            </div>
          </ParallaxLayer>

          {/*page 2 - self intro*/}
          <ParallaxLayer offset={1} speed={1} style={{display:'flex', flexDirection:"row", justifyContent:"center", alignItems:"center", backgroundColor:"white"}}>
              <div style={{backgroundColor:"#987923", width:"35%"}}>
                <img style={{width:"100%", height:"100%"}} src={profile}/>
              </div>

              <div style={{height:"48%",width:"40%", }}>
                <div style={{display:'flex', justifyContent:'center'}}>
                  <h1 style={{fontSize:20}}>About Me</h1>
                </div>

                <div style={{display:'flex', flexDirection:'column', alignItems:'center', }}>
                  <p style={{width:"82%", display:"flex", justifyContent:"flex-end"}}>
                    Hi, I am Zheng Xi, a Computer Engineering undergraduate at National University of Singapore. I am a tea lover  that 
                    spends his free time reading , building nanoblocks , cooking  and checking out dessert spots . My passion lies in the development of Artifical
                    Intelligence as well as its application in our daily lives.
                    <br />
                    <br />
                  </p>
                  <a href='https://www.linkedin.com/in/zheng-xi-eu-b019021bb/' style={{cursor:'pointer'}}>Connect with me via LinkedIn!</a>
                </div>
              </div>
          </ParallaxLayer>

          {/*page 3 - vertical timeline for hardware projects*/}
          <ParallaxLayer offset={2} style={{display:'flex', flex:1, flexDirection:'column'}}>
            <div style={{display:'flex', flex:1, justifyContent:'center', alignItems:'end'}}>
              <p style={{fontSize:20, fontWeight:'bold', color:'white'}}>My Heart(hard)ware Projects</p>
            </div>`

            <div style={{display:'flex', flex:6}}>
              <VerticalTimeline >

                <VerticalTimelineElement
                  className="vertical-timeline-element--work"
                  contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                  contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                  date="2021 Nov"
                  iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}>
                  
                  <p style={{fontWeight:'bold'}}>EG1311 - Design and Make</p>
                  <p style={{fontSize:12, marginBottom:10}}>
                    Upon detection of an obstacle in the immediate vicinty (via the ultrasonic sensor), 
                    the robot activates the servo motor to hurl the ball over the obstacle.
                  </p>
                  
                  <div style={{display:'flex',marginLeft:15} }>
                    <iframe src={`https://www.youtube.com/embed/-e9u6aWoF1Q`} allowFullScreen width="60%" height="100%"/>
                  </div>
                </VerticalTimelineElement>

                <VerticalTimelineElement
                  className="vertical-timeline-element--work"
                  contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                  contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                  date="2022 Nov"
                  iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}>
                  
                  <p style={{fontWeight:'bold'}}>CG1111A - Engineering Principles and Practices I</p>
                  <p style={{fontSize:12, marginBottom:10}}>
                    The mbot conducts straffing based on the ultrasonic and infrared sensors mounted on either 
                    side of the robot. Color detection is achieved via the LED and LDR sensors located at the 
                    bottom of the vehicle. 
                  </p>
                  
                  <div style={{display:'flex',marginLeft:15} }>
                    <iframe src={`https://www.youtube.com/embed/nJr4PbxbPn8`} allowFullScreen width="60%" height="100%"/>
                  </div>
                </VerticalTimelineElement>

                <VerticalTimelineElement
                  className="vertical-timeline-element--work"
                  contentStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
                  contentArrowStyle={{ borderRight: '7px solid  rgb(33, 150, 243)' }}
                  date="2023 Jan"
                  iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}>
                  
                  <p style={{fontWeight:'bold'}}>CG2111A - Engineering Principles and Practices II</p>
                  <p style={{fontSize:12, marginBottom:10}}>
                    In replacement of the breadboard, we created our own circuit by soldering relevant components and wirewrapping them. 
                    The logic of the LED is encoded using baremetal programming. 
                  </p>
                  
                  <div style={{display:'flex',marginLeft:15} }>
                    <iframe src={`https://www.youtube.com/embed/JsvSBepJblw`} allowFullScreen width="60%" height="100%"/>
                  </div>
                </VerticalTimelineElement>  
              </VerticalTimeline>
            </div>
          </ParallaxLayer>

          {/*page 5 - cards for software projects*/}
          <ParallaxLayer offset={4} style={{ display:'flex', flex:1, flexDirection:'column', marginTop:30}}>
            <div style={{display:'flex', flex:1, justifyContent:'center'}}>
              <p style={{marginTop:50, fontSize:25, fontWeight:700, color:'white'}}>My Software Projects (try swiping the cards!)</p>
            </div>

            <div style={{display:'flex', flex:12, justifyContent:'center',}}>
              <div style={{flex:45, width:'70%', display:'flex', justifyContent:'end'}}>
                {UIUX.map(pic => 
                  <TinderCard className='swipe' onSwipe={(dir) => {setIndex(index + 1)}}>
                      <div style={{ backgroundImage: index < swe.length ? 'url(' +swe[index].url + ')': + 'url()'}} className='card'>
                      <h1></h1>
                      </div>
                    </TinderCard>)}
              </div>

              <div style={{flex:55, display:'flex', alignItems:'center'}}>
                <div style={{width:'50%', height:'90%', display:'flex', flexDirection:'column', marginLeft:60}}>
                  <p style={{color:'white', fontWeight:'bold', fontSize:20}}>{index < swe.length? swe[index].title : 'Thats all, Thank You!'}</p>
                  <p style={{color:'white'}}>{index < swe.length? swe[index].des : ''}</p>
                  <a style={{color:'white',cursor: 'pointer'}} href={index < swe.length? swe[index].link : ''}>{index < swe.length? swe[index].link : ''}</a>
                </div>
              </div>
            </div>
          </ParallaxLayer>

          
        </Parallax>
      </div>
    );
}

export default App;
